    font-size: 16px;
    font-family: 'Courier New', Courier, monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scrollbar-color: #2F4858 azure;
    scrollbar-width: thin;
}

h1 {
    line-height: 1.9rem;
}

body {
    background-color: #cadbe4;    
    height: 100vh;
    width: 100vw;    
    display: flex;
    flex-direction: column;
}

#app-grid {
    flex-grow: 1;
    display: grid;
    grid-template-columns: 58px minmax(275px, 20%) minmax(400px, 1fr) 58px;
    grid-template-rows: 25px minmax(200px, 1fr) 20px 3fr 25px;
    overflow: auto;
}

#app-grid > div {
    display: flex;      
}

#header {
    background-color: #80b3d1;    
    grid-column: 1 / 5;
    grid-row: 1 / 2;    
}

#upper-left-side {
    background-color: #cadbe4;
    flex-direction: column;   
    grid-column: 1 / 2;
    grid-row: 2 / 4;      
    padding: 0 4px 0 4px; 
}

#lower-left-side {
    background-color: #cadbe4;
    flex-direction: column;   
    grid-column: 1 / 2;
    grid-row: 4 / 5;      
    padding: 0 4px 0 4px;   
}

#right-side {
    background-color: #cadbe4;
    flex-direction: column;    
    grid-column: 4 / 5;
    grid-row: 2 / 5;
    padding: 0.8em 4px 0 0px;  
}

#period {
    background-color: #cadbe4;    
    grid-column: 2 / 3;
    grid-row: 2 / 3;    
    flex-direction: column;
    justify-content: space-between;    
    padding: 0.8em 0 0.8em 0;                
}

#period button {    
    font-family: 'Courier New', Courier, monospace;    
    background-color: azure;
    border: 2px outset #80b3d1;
    font-size: 1.4rem;
    font-weight: 600;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 5px;
    box-shadow: -1px 2px 3px 1px #9aafb9;    
}

#collection-controls {    
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    gap: 1em;    
}

.collection-control-btn,
#to-do-controls button {
    background-color: azure;
    border-color: #80b3d1;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30% 38%;    
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    padding: 18px;       
    box-shadow: -1px 2px 3px 1px #9aafb9;  
}

#collections-header {    
    background-color: #cadbe4;
    color: #2F4858;
    grid-column: 2 / 3;
    grid-row: 3 / 4;
    display: flex;
    justify-content: center;      
}

#collections-header div {
    width: 95%;
    display: flex;
    justify-content: center;       
}

#collections {
    background-color: #cadbe4;
    grid-column: 2 / 3;
    grid-row: 4 / 5; 
    flex-direction: column;    
    align-items: flex-start;
    gap: 0.6em;        
    margin-bottom: 0.8em;
    overflow: auto;  
}

.collection {
    background-color: azure;
    border: 2px outset #80b3d1;
    display: flex;
    flex-direction: column;
    width: 100%;    
    border-radius: 5px; 
    padding: 0.3em;
    gap: 2px;    
    box-shadow: -1px 2px 3px 1px #9aafb9;       
}

.collection-header {
    font-size: 0.9rem;
    display: flex;
    align-items: center;        
}

.collection-btn {
    min-height: max-content;
    background-color: azure;
    border: none;
    font-size: 1.4rem;
    font-weight: 600;
    text-align: left;
    padding: 0 0 0 3px;     
}

.progress-div {
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
    padding-right: 6px;
}

.collection-btn[data="last-clicked"] {
    color: #80b3d1;
    border: 1px dashed #80b3d1;
    margin: -1px;
}

.todo-div {
    display: flex;
    flex-direction: column;    
}

.to-do {   
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-bottom: 1px dashed #80b3d1;
    padding: 4px 0 4px 0;       
}

.to-do p {    
    display: flex;
    align-items: center;
    flex-grow: 1;
    max-height: 75px;
    overflow-x: hidden;      
}

p[data="last-clicked"]{
    color: #80b3d1;
    border: 1px dashed #80b3d1;
    margin: -1px;    
}

.to-do div {
    min-width: 30px;
}

.clock {
    display: flex;
    justify-content: center;
    height: 25px;      
}

.circle {
    display: flex;
    justify-content: center;
    height: 12px;    
}

.snail {
    display: flex;
    justify-content: center;    
    height: 19px;   
}

.check {
    display: flex;
    justify-content: center;    
    height: 13px;
    margin-bottom: 3px;   
}

.priority-icon-container img {
    height: 100%;
    width: 100%;    
}

.empty {
    color: lightslategray;
    font-style: italic;
    padding: 3px 0 0 3px;
}

#content {
    background-color: #cadbe4;
    flex-direction: column;    
    grid-column: 3 / 4;
    grid-row: 2 / 5;
    padding: 0.8em;
}

.content-container {
    background-color: azure;
    height: 100%;
    width: 100%;
    border: 2px outset #80b3d1;
    border-radius: 5px;
    box-shadow: -1px 2px 3px 1px #9aafb9;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));;
    overflow: auto; 
}

.to-do-details {
    
}

#to-do-controls {
    display: flex;
    flex-direction: column;
    gap: 1em; 
}

#footer {
    background-color: #2F4858;     
    grid-column: 1 / 5;
    grid-row: 5 / 6;    
}
